












_ = require 'lodash'

export default
  data: ->
    alerts: []
  methods:
    close: (visible, i) ->
      if not visible
        @$delete @alerts, i
  created: ->
    _.extend console, 
      log: (msg) =>
        @alerts.push {type: 'success', msg}
      info: (msg) =>
        @alerts.push {type: 'info', msg}
      warn: (msg) =>
        @alerts.push {type: 'warning', msg}
      error: (msg) =>
        @alerts.push {type: 'error', msg}
